.Parallax {
    position: relative;
    width: 100%;
    height: 600px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;

    .Parallax-background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      background-color: transparent;
      list-style: none;

      > li {
        width: 20px;
        height: 20px;
        position: absolute;
        animation: all 2s linear infinite;
        border: 3px solid #1294bf71;
        opacity: 100%;
        bottom: 0%;
        border-radius: 5px;
      }
    }

    > section {
      z-index: 1;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-bottom: 120px;

      > svg {
        fill: var(--secondary);
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
      }

      .box {
        width: 100%;
        max-width: 500px;
        height: 300px;
        border-radius: 5px;
        box-shadow: 2px 3px 7px #00000099;
        border: 1px solid #ffffff4d;
        padding: 20px;
        text-align: center;
        text-shadow: 0 1px 1px #00000066;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        backdrop-filter: blur(5px);
        background: linear-gradient(90deg, #ffffff54, #ffffff40);
      }

      h2 {
        text-align: center;
        font-weight: 400;
        font-size: 30px;
      }

      h3 {
        font-size: 20px;
        font-weight: 100;
      }
    }
}